import API from '../backend/api'

export default {
   
   
    getFolderStructure({
        commit
    }, payload_data) {
        // console.log(payload_data);

        return new Promise(resolve => {
            API.getFolderStructure_API(payload_data, data => {
                commit('SAVE_FOLDER_STRUCTURE', data)
                resolve();
            })
        });

    },
    getFoldersById({
        commit
    }, payload_data) {
        // console.log(payload_data);

        return new Promise(resolve => {
            API.getFoldersById_API(payload_data, data => {
                commit('SAVE_C_FOLDER', data)
                resolve();
            })
        });

    },

    insertNewFolder({
        commit
    }, folder) {
        return new Promise(resolve => {
            API.insertNewFolderDB(folder, folderDB => {
                // commit('SAVE_STAFF', project)
                commit('NEW_FOLDER', folderDB);
                resolve();
            })
        });
    },
   
   

}
